@use 'variables' as v;

@mixin telefono {
    @media (max-width: v.$telefono) {
        @content;
    }
}

@mixin tablet {
    @media (max-width: v.$tablet) {
        @content;
    }
}

@mixin escritorio {
    @media (max-width: v.$escritorio) {
        @content;
    }    
}

@mixin escritorioXL {
    @media (max-width: v.$escritorioXL) {
        @content;
    }    
}

@mixin grid($columnas, $espaciado) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espaciado;
}

@mixin imagenFondo {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

@mixin boton ($fondo, $color) {
    display: inline-block;
    margin-top: 2rem;
    color: $color;
    background: $fondo;
    padding: 1rem 2rem;
    border-radius: 2rem;
    transition: all .3s ease;

    &:hover{
        background: transparent;
        border: 1px solid $fondo;
        color: $fondo;
    }
}

@mixin borde ($color) {
    position: relative;
    padding: .5rem;

    &:before{
        content: '';
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 2px;
        background: $color;
        transition: all .3s ease;
    }
}

@mixin bordeAnimado($color) {
    position: relative;
    padding: .5rem;

    &:before{
        content: '';
        display: block;
        position: absolute;
        left: 50%;
        bottom: 0;
        width: 0;
        height: 2px;
        background: $color;
        transition: all .3s ease;
    }

    &:hover:before{
        left: 0;
        width: 100%;
   }
}

