@use 'variables' as v;
@use 'mixins' as m;

html{
    font-size: 62.5%;
}

body{
    font-size: 1.6rem;
    font-family: v.$open;
}

.contenedor{
    margin: 0 auto;
    width: 90%;
    max-width: 1200px;
}

img{
    width: 100%;
    height: 100%;
}

a{
    text-decoration: none;
}

h1{
    font-size: 8.8rem;
}

h2{
    font-size: 4.8rem;

    @include m.telefono{
        font-size: 2.4rem;
    }
}

h3{
    font-size: 3.2rem;

    @include m.tablet{
        font-size: 2.2rem;
    }
}

body{

    transition: all .3s ease;

    &.noche{

        background: v.$fondo;
    
        h2{
            color: v.$rosa;
        }
        
        h3{
            color: v.$fondo;
        }
    
        h1, a, p, textarea, i, input, label, figcaption, blockquote{              
            color: v.$grisSuave; 
        }

        #cont-resumen{
            a{
                transition: all .3s ease-in;

                i{
                    color: v.$fondo;
                }

                
                &:hover{
                    i{
                        color: v.$grisSuave;
                    }
                }
            }
        }

        .proyecto__enlaces{
            a{
                transition: all .3s ease-in;

                i{
                    color: v.$grisSuave;
                }

                
                &:hover{
                    i{
                        color: v.$fondo;
                    }
                }
            }
        }

        .footer{
            .contenedor{
                border-top: 2px solid v.$gris;
            }
        }
    }

    &.dia{
        background: v.$grisSuave;
    
        h2{
            color: v.$rosa;
        }
        
        h3{
            color: v.$grisSuave;
        }
    
        h1, a, p, textarea, i, input, label, figcaption, blockquote{              
            color: v.$fondo; 
        }

        #cont-resumen{
            a{
                transition: all .3s ease-in;

                i{
                    color: v.$grisSuave;
                }

                
                &:hover{
                    i{
                        color: v.$fondo;
                    }
                }
            }
        }

        .proyecto__enlaces{
            a{
                transition: all .3s ease-in;

                i{
                    color: v.$fondo;
                }

                
                &:hover{
                    i{
                        color: v.$grisSuave;
                    }
                }
            }
        }

        .footer{
            .contenedor{
                border-top: 2px solid v.$gris;
            }
        }
    }
}

.logo{
    font-size: 2.4rem;

    #mayor, #menor{
        color: v.$rosa;
        font-weight: 700;
    }
    
    .diagonal{
        color: v.$gris;
        font-weight: 700;
    }
}

.no-ver{
    display: none;
}