@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer{
    margin-top: 5rem;

    .contenedor{
        padding: 3rem 0 1rem;

        .footer_info{
            display: flex;
            justify-content: space-between;
            align-items: flex-start;

            @include m.telefono{
                flex-direction: column-reverse;
                align-items: center;
                margin-bottom: 2rem;
            }

            .cont-redes{
                text-align: right;

                @include m.telefono{
                    width: 60%;
                    text-align: center;
                }

                p{
                    margin-bottom: 2rem;

                    @include m.telefono{
                        margin-bottom: 1rem;
                    }
                }

                .enlaces{


                    a{
                        font-size: 2.4rem;
                        margin-left: 2rem;
                        i{
                            transition: all .5s ease;

                            &:hover{
                                transform: scale(.9);
                            }
                        }

                        @include m.telefono{
                            display: block;
                            margin: 0;
                        }
                    }

                    @include m.telefono{
                        margin-bottom: 2rem;
                        display: flex;
                        justify-content: space-between;
                    }
                }
            }
        }

        .copyright{
            text-align: center;

            @include m.telefono{
                font-size: 1.2rem;
            }
        }
    }

}