@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    
    width: 100%;
    height: 100vh;
    
    .contenedor{
        position: relative;

        .header__nav{
            height: 9.6rem;
            display: flex;
            justify-content: space-between;
            align-items: center;

            &.fijo{
                position: fixed;
                top: 0;
                left: 0;
                width: 100%;
                z-index: 30;
                background: v.$grisSuave;
                box-shadow: 0px 7px 9px 0px rgba(0,0,0,0.5);
                transition: all .3s ease;
                padding: 0 5%;

                a{
                    color: v.$fondo;
                    
                }

                .nav__enlaces{
                    a{
                        @include m.bordeAnimado(v.$fondo);
                    }
                }

                .menu-ham{
                    i{
                        color: v.$fondo;
                    }
                }


            }

            .nav__enlaces{
                a{
                    margin-right: 2.4rem;
    
                    &:last-of-type{
                        margin-right: 0;
                    }

                    position: relative;

                    @include m.bordeAnimado(v.$blanco);
                }

                @include m.telefono{
                    z-index: 20;
                    position: fixed;
                    top: 0;
                    right: 0;
                    height: 100vh;
                    width: 100%;
                    background: v.$grisSuave;
                    transition: all .6s ease;
                    padding-top: 20%;
                    display: flex;
                    justify-content: space-evenly;
                    align-items: center;
                    flex-direction: column;
                    clip-path: circle(0% at 100% 0);

                    a{
                        margin-right: 0;
                        display: block;
                        text-align: center;
                        color: v.$fondo;
                        font-size: 3.6rem;
                        font-weight: 300;
                        @include m.bordeAnimado(v.$fondo);
                    }

                    &.abrir{
                        clip-path: circle(141.4% at 100% 0);
                    }
                }
            }

            .menu-ham{
                display: none;

                @include m.telefono{
                    display: block;
                    z-index: 21;
                }
                
                .bx-menu-alt-right{
                    display: block;
                    font-size: 3.6rem;
                    cursor: pointer;

                    &.ocultar{
                        display: none;
                    }
                }

                .bx-x{
                    display: none;

                    &.mostrar{
                        color: v.$fondo;
                        font-size: 3.6rem;
                        display: block;
                        cursor: pointer;
                        transition: all .3s ease;
                    }
                }
            }

        }
    
        .principal{
            height: calc(100vh - 9.6rem);
            @include m.grid(2, 4.8rem);

            @include m.tablet{
                @include m.grid(1, 1rem);
                padding-bottom: 15%;
            }

            .principal_info{
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: flex-start;

                .saludo{
                    font-size: 3.2rem;
                    color: v.$gris;
                    font-weight: 300;
                }

                h1{
                    margin-bottom: 2rem;
                }

                .presentacion{
                    font-size: 3.2rem;
                    color: v.$rosa;
                    font-style: italic;
                }

                @include m.escritorioXL{
                    .saludo, .presentacion{
                        font-size: 2.8rem;
                    }

                    h1{
                        font-size: 7.2rem;
                    }
                }

                @include m.escritorio{
                    .saludo, .presentacion{
                        font-size: 2.4rem;
                    }

                    h1{
                        font-size: 6.2rem;
                    }
                }

                @include m.tablet{
                    text-align: center;
                    align-items: center;
                }

                @include m.telefono{
                    h1{
                        font-size: 4.8rem;
                    }
                }
            }

            .cont-imagen{
                display: flex;
                justify-content: center;
                align-items: center;

                @include m.tablet{
                    justify-content: flex-start;
                    margin: 0 auto;
                    width: 60%;
                }
            }
        }

        .btn-seguir{
            position: absolute;
            bottom: 3rem;
            left: 47%;
            font-size: 4.2rem;
            transition: all .3s ease-in-out;

            &:hover{
                transform: translateY(-10px);
            }
        }

        .btn-darkmood{
            position: absolute;
            right: 0px;
            bottom: 50px;
            width: 5rem;
            height: 5rem;
            border-radius: 1rem;
            cursor: pointer;
            transition: all .3s ease;
            box-shadow: -1px 3px 20px 0px rgba(0,0,0,0.3);

            &.dia{
                background: v.$fondo;
            }

            &.noche{
                background: v.$grisSuave;
            }

            .claro{
                display: none;

                &.activo{
                    display: block;
                }

            }

            .oscuro{
                display: none;

                &.activo{
                    display: block;
                }
            }
        }

    }

}


.dia{
    .header{
        .contenedor{
            .header__nav{
                .nav__enlaces{

                    a{
                        @include m.bordeAnimado(v.$fondo);
                    }

                    @include m.telefono{
                        background: v.$fondo;

                        a{
                            color: v.$grisSuave;

                            @include m.bordeAnimado(v.$grisSuave);
                        }
                    }
                }

                .menu-ham{
                    .bx-x.mostrar{
                        color: v.$grisSuave;
                    }
                }
            }
        }
    }
}
