@use 'base/variables' as v;
@use 'base/mixins' as m;

.webp{
    .frase{
        background-image: v.$gradiente, url('../img/home.webp');
    }
}

.no-webp{
    .frase{
        background-image: v.$gradiente, url('../img/home.jpg');
    }
}

.frase{
    margin-top: 5rem;
    height: 35rem;
    @include m.imagenFondo();
    background-attachment: fixed;

    .contenido{
        width: 40%;
        margin: 0 auto;
        height: 100%;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        @include m.tablet{
            width: 75%;
        }

        blockquote{
            color: v.$blanco;
            font-size: 4.8rem;
            text-align: center;
            font-style: italic;
            font-weight: 300;

            @include m.escritorio{
                font-size: 3.2rem;
            }
        }

        p{
            margin-top: 3rem;
            font-size: 2.4rem;
            color: v.$grisSuave;
        }
    }

}
