@use 'base/variables' as v;
@use 'base/mixins' as m;

.contacto{
    margin-top: 5rem;
    padding-bottom: 5rem;

    @include m.tablet{
        margin-top: 3rem;
        padding-bottom: 3rem;
    }

    .formulario{
        display: flex;
        flex-direction: column;
        justify-content: center;

        .cont-input{
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            position: relative;
            margin-top: 2.4rem;

            label{
                font-size: 1.8rem;
                font-weight: 300;
                position: absolute;
                top: 3rem;
                left: 2rem;
                transition: all .3s ease-in-out;

                &.focus{
                    font-size: 1.6rem;
                    top: 0;
                    left: 0;
                }
            }

            &.cont__corto{
                height: 6rem;
                display: flex;
                flex-direction: column;
                justify-content: flex-end;

                input{
                    width: 80%;
                    height: 3.2rem;
                    background: transparent;
                    border: none;
                    border-bottom: 1px solid v.$gris;
                    padding: 0 2rem;

                    @include m.tablet{
                        width: 100%;
                    }
                }
            }

            &.input__mensaje{
                height: 20rem;

                textarea{
                    min-width: 80%;
                    max-width: 80%;
                    min-height: 17.25rem;
                    max-height: 17.25rem;   
                    background: transparent;
                    border: none;
                    border-bottom: 1px solid v.$gris;
                    padding: 1rem 2rem;

                    @include m.tablet{
                        min-width: 100%;
                        max-width: 100%;
                    }
                }
            }

            i{
                opacity: 0;
                position: absolute;
                bottom: .8rem;
                right: 22%;

                &.check{
                    opacity: 1;
                    
                }

                @include m.tablet{
                    right: 0;
                }
            }
        }

        .submit{
            width: 80%;
            margin-top: 2.4rem;
            border: none;
            @include m.boton(v.$grisSuave, v.$fondo);

            @include m.tablet{
                width: 100%;
            }
        }

        .mensaje{
            margin-top: 3rem;
            font-size: 2.4rem;

            &.error{
                color: v.$rosa;
            }
        }

        .submit{
            &.desactivado{
                cursor: not-allowed;
            }
        }
    }

    .cont-contacto{
        margin-top: 5rem;
        @include m.grid(2, 2rem);

        @include m.tablet{
            margin-top: 3rem;

            @include m.grid(1, 0);
        }

        .cont-imagen{
            padding: 5rem;

            @include m.tablet{
                display: none;
            }
        }
    }
}

.dia{
    .formulario{
        .submit{
            @include m.boton(v.$fondo, v.$grisSuave);
        }
    }
}