@use 'base/variables' as v;
@use 'base/mixins' as m;

.portafolio{
    margin-top: 5rem;
    padding-bottom: 5rem;

    @include m.telefono{
        margin-top: 3rem;
        padding-bottom: 3rem;
    }

    .cont-proyectos{
        margin-top: 5rem;
        @include m.grid(3, 3rem);

        @include m.escritorio{
            @include m.grid(2, 2rem);
        }

        @include m.telefono{
            @include m.grid(1, 2rem);
        }

        .proyecto{
            position: relative;
            overflow: hidden;
            border-radius: 1rem;

            picture{
                img{
                    border-top-left-radius: 1rem;
                    border-top-right-radius: 1rem;
                }
            }

            @include m.tablet{
                position: static;
                height: auto;
                overflow: visible;
                display: flex;
                flex-direction: column;
            }

            @include m.telefono{
                margin-bottom: 0;
            }

            &::after{
                content: '';
                position: absolute;
                width: 100%;
                height: 110%;
                top: -1rem;
                left: -30rem;
                clip-path: polygon(0 0, 40% 0, 100% 100%, 60% 100%);
                background: linear-gradient(to right, #355c7d, #6c5b7b, #c06c84);
                z-index: 10;
                transition: all .6s ease-in-out;

                @include m.tablet{
                    display: none;
                }
            }

            &::before{
                content: '';
                position: absolute;
                width: 100%;
                height: 110%;
                top: -1rem;
                left: 30rem;
                clip-path: polygon(0 0, 40% 0, 100% 100%, 60% 100%);
                background: linear-gradient(to right, #355c7d, #6c5b7b, #c06c84);
                z-index: 10;
                transition: all .6s ease-in-out;

                @include m.tablet{
                    display: none;
                }
            }

            .proyecto__hover{
                position: absolute;
                background: v.$grisSuave;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                opacity: 0;
                transition: all .3s .1s ease-in-out;

                @include m.tablet{
                    position: static;
                    opacity: 1;
                    padding: 1rem 0;
                    border-bottom-left-radius: 1rem;
                    border-bottom-right-radius: 1rem;
                }

                

                .proyecto__enlaces{
                    margin-top: .5rem;
                    width: 80%;
                    display: flex;
                    justify-content: space-evenly;

                    @include m.tablet{
                        flex-basis: 30;
                        width: 100%;

                        h3{
                            margin-top: 1rem;
                        }
                    }

                    a{
                        @include m.boton(v.$fondo, v.$grisSuave);

                        @include m.tablet{
                            margin: 1rem 0;
                            font-size: 1.2rem;
                        }
                    }
                }
            } 

            &:hover{
                .proyecto__hover{
                    opacity: 1;
                }

                &::after{
                    left: 30rem;
                }

                &::before{
                    left: -30rem;
                }
            }
        }
    }
}

.dia{
    .portafolio{
        .cont-proyectos{
            .proyecto{
                .proyecto__hover{
                    background: v.$fondo;

                    .proyecto__enlaces{
                        a{
                            @include m.boton(v.$grisSuave, v.$fondo);
                        }
                    }
                }
            }
        }
    }
}