  right: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 1s ease;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}
body .bienvenida.remover {
  clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
}
