@use 'base/variables' as v;
@use 'base/mixins' as m;

.acerca{

    margin-top: 5rem;
    padding-bottom: 5rem;

    @include m.tablet{
        padding-bottom: 3rem;
    }

    .contenedor{
        @include m.grid(2, 4.8rem);

        @include m.escritorio{
            @include m.grid(1, 3.6rem);
        }

        .cont-imagen{
            padding: 0 5rem;
            height: 48rem;
            background: v.$grisSuave;
            border-radius: 1rem;

            /*.imagen{
                border-radius: 1rem;
                width: 100%;
                height: 100%;
                background: v.$grisSuave;
            }*/
            


            @include m.tablet{
                padding: 0;
                height: 35rem;
            }
        }

        .cont-info{
            padding: 2.5rem;

            h2{
                margin-bottom: 5rem;
                
                @include m.telefono{
                    margin-bottom: 3rem;
                }
            }

            @include m.tablet{
                padding: 0;
            }

            .apartados{
                display: inline-block;
                margin-right: 2.4rem;
                margin-bottom: 3rem;
                font-size: 2.4rem;

                @include m.bordeAnimado(v.$blanco);

                &.activo{
                    @include m.borde(v.$blanco);
                }

                @include m.telefono{
                    font-size: 1.8rem;
                }
            }

            #cont-resumen{
                display: none;
                transition: opacity .3s ease-in;

                p{
                    margin-bottom: 2rem;
                    font-size: 1.8rem;
                    font-weight: 300;
                }
    
                .cv{
                    display: inline-block;
                    @include m.boton(v.$grisSuave, v.$fondo);

                }

                &.activo{
                    display: block;
                }
            }

            #cont-herramientas{
                transition: opacity .3s ease-in;
                display: none;
                text-align: center;

                .iconos-herramientas{
                    transition: all .3s ease-in-out;

                    i{
                        font-size: 5.2rem;
                        transition: all .3s ease-in-out;
                    }

                    figcaption{
                        font-size: 1.2rem;
                    }

                    svg{
                        fill: v.$grisSuave;
                        transform: scale(.8);
                    }

                    &:hover{
                        transform: scale(.9);
                    }
                }

                &.activo{
                    @include m.grid(4, 2rem);
                }
            }
        }
    }
}

.dia{
    .acerca{
        .contenedor{
            .cont-info{

                .apartados{
                    @include m.bordeAnimado(v.$fondo);

                    &.activo{
                        @include m.borde(v.$fondo);
                    }
                }

                #cont-resumen{
                    .cv{
                        @include m.boton(v.$fondo, v.$grisSuave);
                    }
                }

                #cont-herramientas{

                    .iconos-herramientas{
                        svg{
                            fill: v.$fondo;
                        }
                    }
                }
            }
        }
    }
}